.contactForm {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  border: 2px solid;
  width: 400px;
  justify-content: space-evenly;
  height: 170px;
  padding-left: 30px;
}
button[type='submit'] {
  background-color: transparent;
  border: 1px solid #d1cece;
  border-radius: 8px;
  margin-right: 5px;
  /* width: 60px; */
  height: 30px;
  font-size: 16px;
  box-shadow: 0 2px 0 0 #d1cece;
}

button[type='submit']:active {
  background-color: rgb(0, 149, 255);
  color: #fff;
  box-shadow: none;
  border-color: rgb(0, 149, 255);
}
