.contact {
  display: flex;
  align-items: center;
}

button[type='button'] {
  background-color: transparent;
  border: 1px solid #d1cece;
  border-radius: 8px;
  margin-right: 5px;
  /* width: 60px; */
  height: 30px;
  font-size: 16px;
  box-shadow: 0 2px 0 0 #d1cece;
  margin-left: 10px;
}

button[type='button']:active {
  background-color: red;
  color: #fff;
  box-shadow: none;
  border-color: red;
}
